<template>
  <header class="header">
    <div class="header__inner container">
      <h1 class="header__inner-title"><span class="header__inner-title-span1">About</span> <span
          class="header__inner-title-span2">Me.</span></h1>
      <nav class="header__inner-navbar">
        <ul class="header__inner-navbar-list">
          <li><a href="#" class="header__inner-navbar-list-item-link" @click="scroll(0)">Home</a></li>
          <li><a href="#" class="header__inner-navbar-list-item-link" @click="scroll(700)">About</a></li>
          <li><a href="#" class="header__inner-navbar-list-item-link" @click="scroll(1400)">Service</a></li>
          <li><a href="#" class="header__inner-navbar-list-item-link" @click="scroll(2250)">Portfolio</a></li>
          <li><a href="#" class="header__inner-navbar-list-item-link" @click="scroll(3100)">Resume</a></li>
          <li><a href="#" class="header__inner-navbar-list-item-link" @click="scroll(3920)">Contact</a></li>
          <li><img class="header__inner-navbar-list-img" src="@/assets/img/toggle.png" alt="" @click="lightMode"></li>
        </ul>
      </nav>
    </div>
  </header>
</template>

<script>
export default {
  data() {
    return {

    }
  },
  methods: {
    scroll(e) {
      window.scroll({
        top: e,
        left: 0,
        behavior: "smooth"
      })
    },
    lightMode(){
      let value = document.body.style.getPropertyValue("--darkblue")

      if(value == "#020617"){
        document.body.style.setProperty("--darkblue", "#588787");
        document.body.style.setProperty("--lightblue", "#b3c9c9");
        document.body.style.setProperty("--lightgrey", "white");
        document.body.style.setProperty("--grey", "white");
      } else{
        document.body.style.setProperty("--darkblue", "#020617");
        document.body.style.setProperty("--grey", "#6b7280");
        document.body.style.setProperty("--lightblue", "#111827");
      }
    }
  },
}
</script>

<style lang="scss" scoped></style>